
.bodyAll {
    height: 90vh;
}


/* navHeader Mobile */

@media (min-width: 50px)and (max-width: 768px) {
    .navHeader {
        height: 8vh;
    }
}


/* navHeader Desktop */

@media (min-width: 768px) {
    .navHeader {
        height: 8vh;
    }
}

.navHeader_container {
    background: #FFFFFF 0% 0% no-repeat padding-box;
    opacity: 1;
    box-shadow: 0px 3px 6px #00000029;
    z-index: 1;
}

.body {
    height: 100%;
    background-color: #E8EAEC !important;
    
}


/* nav left */

.navLeft {
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    opacity: 1;
}

#leftNavTitls {
    text-align: left;
    font: normal normal  16px/48px SF Pro Display;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: 0px;
    opacity: 1;
}

.navLeft_icon_content {
    margin-top: 100px;
}

.content {
    background: #FFFFFF 0% 0% no-repeat padding-box;
    height: 96%;
    overflow-y: auto;
    /* border: 3px dashed #E8E7F2; */
    /* border-style: dashed */
    border-radius: 15px !important;

}

.content::-webkit-scrollbar {
    width: 0em;
}

.configurator {
    background: #3A405E;
    margin: -10px;
    height: 100%;
    border: 1px solid #707070;
    border-radius: 80px 0px 0px 0px;
    z-index: 2;
    width: 250px !important;
}

.configurator_container {
    height: 80%;
}

.configurator_main {
    height: 100%;
    overflow-y: auto;
}

.configurator_main::-webkit-scrollbar {
    width: 0em;
}

.configurator_header {
    margin-top: 25%;
}

.configurator_header_title {
    text-align: center;
    font: normal normal medium 18px/24px Roboto;
    font-size: 14px;
    letter-spacing: 0.32px;
    color: #FFFFFF;
    text-transform: capitalize;
    opacity: 1;
}

.configurator_footer {
    position: fixed;
    width: 260px;
    right: 0;
    bottom: 0;
    height: 50px;
    background: #0A0E1B 0% 0% no-repeat padding-box;
}

.publish {
    background: #A66DFF 0% 0% no-repeat padding-box;
    text-align: center;
    width: 100%;
    height: 60%;
    font: normal normal medium 12px/16px SF Pro Display;
    letter-spacing: 0px;
    color: #EBEAEA;
    opacity: 1;
}

.upDatePublish {
    background: #FAFAFA 0% 0% no-repeat padding-box;
    border: 1px solid #A66DFF;
    text-align: center;
    /* width: 100%; */
    height: 60%;
    font: normal normal medium 12px/16px SF Pro Display;
    letter-spacing: 0px;
    color: #A66DFF;
    opacity: 1;
}

.savePublish {
    background: #FF5F5F 0% 0% no-repeat padding-box;
    text-align: center;
    /* width: 100%; */
    height: 60%;
    font: normal normal medium 12px/16px SF Pro Display;
    letter-spacing: 0px;
    color: #EBEAEA;
    opacity: 1;
}

.col-1 {
    flex: 0 0 6.333333%;
    max-width: 4.333333% !important;
}

a[title]:hover:after {
    content: attr(title);
    background: black;
    text-align: center;
    color: white;
    position: absolute;
    width: 120px;
    z-index: 3;
}

.toolTipPublish {
    color: #FFFFFF;
    height: 44px;
    background: #FF5F5F 0% 0% no-repeat padding-box;
    opacity: 0.64;
}

.toolTipSave {
    color: #FFFFFF;
    height: 44px;
    background: #8651D9 0% 0% no-repeat padding-box;
    opacity: 0.64;
}

.col{
    padding-right: 0px !important; 
    padding-left: 2% !important;
}

.bg-ligh{
    background-color: #0A0E1B !important;
}

d{
